
const Noticia = ({noticia}) => {

    const { urlToImage, url, title, description, source} = noticia;

    let imagen = '';

    if(urlToImage !== '') {
        imagen = <img src={urlToImage} alt={title} className="card-img-top" />
    }

    return (
        <div>
            desde Noticia
        </div>
    )
}

export default Noticia
